@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Karla:wght@300;400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anybody&display=swap');

@font-face {
    font-family: 'icomoon';
    src:  url('/dist/fonts/icons.eot?afwhip');
    src:  url('/dist/fonts/icons.eot?afwhip#iefix') format('embedded-opentype'),
    url('/dist/fonts/icons.ttf?afwhip') format('truetype'),
    url('/dist/fonts/icons.woff?afwhip') format('woff'),
    url('/dist/fonts/icons.svg?afwhip#icons') format('svg');
    font-weight: normal;
    font-style: normal;
}
.icomoon {
    position: relative;
}
.icomoon:after ,
.icomoon:before {
    font-family: icomoon!important;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    position: absolute;
}